h1,h2,h3,h4,h5,h6 {
  color: $heading-foreground-color;
  text-align: left;
  padding: 0.3rem 0;
  margin: 0.3rem 0;

  small {
    font-size: 75%;
    color: darken($heading-foreground-color, 25%);
  }

  .btn {
    float: right;
    margin-left: 0.3rem;
    margin-top: 0.2rem;
  }

  .pull-right {
    margin-left: 0.3rem;
    margin-top: 0.2rem;
  }

  small.pull-right {
    margin-top: 0.3rem;
  }

  &::after {
    content: ' ';
    display: block;
    clear: both;
  }
}

h1 {
  font-size: 1.4rem;
  border-bottom: 1px solid lighten($heading-foreground-color, 25%);

  small {
    font-size: 60%;
  }
}

h2 {
  font-size: 1.3rem;
}

.alert {
  h1, h2, h3, h4, h5, h6 {
    color: inherit;
  }
}


/* HR headings (kinda hacky) */
hr[title] {
  position: relative;
  text-align: center;

  &::before {
    content: attr(title);
    position: relative;
    top: -0.7em;
    background: white;
    padding: 0 15px;
  }
}